import axios from "axios";
import { createSlice } from "@reduxjs/toolkit";
import { API_BASE_URL } from "../../../utils/constant";
import {ADD_TO_CART, GET_ORDER_LIST, GET_WISHLIST, USER_CART, USER_PROFILE} from "../../../utils/apiConstants";
import { ADD_TO_WISHLIST } from "../../../utils/apiConstants";
import {  toast } from 'react-toastify';
const initialAuthState  = {
    isLoading: false,
    isError: null,
    userProfileRes: null,
    isSuccess: false,
    AddToCartRes:null,
    GetCartRes:null,
    AddtoWishlistRes:null,
    GetWishlistRes:null,
    userProfileUpdateRes:null,
    ResetPasswordRes:null
};

const UserSlice = createSlice({
    name: 'User',
    initialState: initialAuthState,
    reducers: {
        UserProfileAPIRequest(state) {            
            state.isLoading = true;
        },
        UserProfileAPISuccess(state, action){
            state.userProfileRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        UserProfileAPIFailure(state, action){    
            state.isLoading = false;
        },

        UserProfileUpdateAPIRequest(state) {            
            state.isLoading = true;
        },
        UserProfileUpdateAPISuccess(state, action){
            state.userProfileUpdateRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        UserProfileUpdateAPIFailure(state, action){    
            state.isLoading = false;
        },

        ResetPasswordAPIRequest(state) {            
            state.isLoading = true;
        },
        ResetPasswordAPISuccess(state, action){
            state.ResetPasswordRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        ResetPasswordAPIFailure(state, action){    
            state.isLoading = false;
        },

        AddToCartAPIRequest(state) {            
            state.isLoading = true;
        },
        AddToCartAPISuccess(state, action){
            state.AddToCartRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        AddToCartAPIFailure(state, action){    
            state.isLoading = false;
        },

        
        GetCartItemsAPIRequest(state) {            
            state.isLoading = true;
        },
        GetCartItemsAPISuccess(state, action){
            state.GetCartRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        GetCartItemsAPIFailure(state, action){    
            state.isLoading = false;
        },


        AddToWishlistAPIRequest(state) {            
            state.isLoading = true;
        },
        AddToWishlistAPISuccess(state, action){
            state.AddtoWishlistRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        AddToWishlistAPIFailure(state, action){    
            state.isLoading = false;
        },


        GetWishlistAPIRequest(state) {            
            state.isLoading = true;
        },
        GetWishlistAPISuccess(state, action){
            state.GetWishlistRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        GetWishlistAPIFailure(state, action){    
            state.isLoading = false;
        },
    }
});


export const UserProfileGetAction = (id) => {
    return async (dispatch) => {
        dispatch(UserActions.UserProfileAPIRequest());
        let url= `${API_BASE_URL}${USER_PROFILE}/${id}`;   
        let token = localStorage.getItem("token")
        axios({
            method: 'GET',
            url: url,
            headers: {
                'Authorization': token
              }
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(UserActions.UserProfileAPISuccess(response?.data?.data))
              console.log(response.data,"profile")
            }
            else{         
                dispatch(UserActions.UserProfileAPIFailure())
            }
        })
        .catch(err => dispatch(UserActions.UserProfileAPIFailure()))
    }
}

export const UserProfileUpdateAction = (id,data) => {
    return async (dispatch) => {
        dispatch(UserActions.UserProfileUpdateAPIRequest());
        let url= `${API_BASE_URL}updateUserProfile/${id}`;   
        let token = localStorage.getItem("token")
        axios({
            method: 'PATCH',
            url: url,
            headers: {
                'Authorization': token
              },
              data:data
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(UserActions.UserProfileUpdateAPISuccess(response?.data?.data))
              toast.success(response?.data?.message, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            }
            else{         
                dispatch(UserActions.UserProfileUpdateAPIFailure())
            }
        })
        .catch(err =>{
            toast.error("Something Went Wrong!", {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            dispatch(UserActions.UserProfileUpdateAPIFailure())})
    }
}

