export const AddToCartAction = (userId,productId,navigation) => {
    let data ={
        "user_id": userId,
        "product_id": productId
    }
    let token = localStorage.getItem("token")
    return async (dispatch) => {
        dispatch(UserActions.AddToCartAPIRequest());
        let url= `${API_BASE_URL}${ADD_TO_WISHLIST}`;   
        axios({
            method: 'POST',
            url: url,
            headers: {
                'Authorization': token 
              },
            data:data
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(UserActions.AddToCartAPISuccess(response?.data?.data))
              console.log(response?.data)
              navigation(process.env.PUBLIC_URL + "/wishlist")
            }
            else{         
                dispatch(UserActions.AddToCartAPIFailure())
            }
        })
        .catch(err => dispatch(UserActions.AddToCartAPIFailure()))
    }
}

