import axios from "axios";
import { createSlice } from "@reduxjs/toolkit";
import { API_BASE_URL } from "../../../utils/constant";
import { LOGIN, OTP_VERIFY, REGISTER } from "../../../utils/apiConstants";
import { ToastContainer, toast } from 'react-toastify';
import { AddToCartAction, AddToWishlistAction } from "../userSlice/userSlice";
import { getBottomNavigationUtilityClass } from "@mui/material";
const initialAuthState  = {
    isLoading: false,
    isError: null,
    categoriesListRes: null,
    isSuccess: false,
};

const AuthSlice = createSlice({
    name: 'Auth',
    initialState: initialAuthState,
    reducers: {
        RegisterAPIRequest(state) {            
            state.isLoading = true;
        },
        RegisterAPISuccess(state, action){
            state.categoriesListRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        RegisterAPIFailure(state, action){    
            state.isLoading = false;
        },

        ForgetPasswordAPIRequest(state) {            
            state.isLoading = true;
        },
        ForgetPasswordAPISuccess(state, action){
            state.categoriesListRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        ForgetPasswordAPIFailure(state, action){    
            state.isLoading = false;
        },

        OtpVerifyAPIRequest(state) {            
            state.isLoading = true;
        },
        OtpVerifyAPISuccess(state, action){
            state.categoriesListRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        OtpVerifyAPIFailure(state, action){    
            state.isLoading = false;
        },

        LoginAPIRequest(state) {            
            state.isLoading = true;
        },
        LoginAPISuccess(state, action){
            state.categoriesListRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        LoginAPIFailure(state, action){    
            state.isLoading = false;
        },
    }
});


export const RegisterAction = (data,setOpen) => {
    console.log(data,"dataaaaaaaaaa")
    return async (dispatch) => {
        dispatch(AuthActions.RegisterAPIRequest())
        let url= `${API_BASE_URL}${REGISTER}`;   
        axios({
            method: 'POST',
            url: url,
            data:data
        })
        .then(response => { 
            console.log("dataaaaa",response.data)
            if(response?.data?.status == 1){
                console.log("dataaaaaIf",response.data)
                toast.success(response?.data?.message, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    });
              setOpen(true)
              dispatch(AuthActions.RegisterAPISuccess(response.data))
            }
            else{     
                console.log("dataaaaElse",response.data)    
                dispatch(AuthActions.RegisterAPIFailure())
                toast.error(response?.data?.message, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    });
            }
        })
        .catch(err => {            
            toast.error("Something Went Wrong!")      
        })
    }
}

