export const OtpVerifyAction = (data,setOpen,settabSelected,ResetRegisterStates) => {
    return async (dispatch) => {
        dispatch(AuthActions.OtpVerifyAPIRequest())
        let url= `${API_BASE_URL}${OTP_VERIFY}`;   
        axios({
            method: 'POST',
            url: url,
            data:data
        })
        .then(response => { 
            if(response?.data?.status == 1){
              dispatch(AuthActions.OtpVerifyAPISuccess(response.data))
              setOpen(false)
              ResetRegisterStates()
              toast.success(response?.data?.message, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                settabSelected("Login")
            }
            else{         
                dispatch(AuthActions.OtpVerifyAPIFailure())
                toast.error(response?.data?.message, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    });
            }
        })
        .catch(err => dispatch(AuthActions.OtpVerifyAPIFailure()))
    }
}

export const LoginAction = (data, navigation,purpose=getBottomNavigationUtilityClass,product=null) => {
    return async (dispatch) => {
        dispatch(AuthActions.LoginAPIRequest())
        let url= `${API_BASE_URL}${LOGIN}`;   
        axios({
            method: 'POST',
            url: url,
            data:data
        })
        .then(response => { 
            if(response?.data?.status == 1){
              dispatch(AuthActions.LoginAPISuccess(response.data))
             
                localStorage.setItem("userId",response?.data?.user_id)
                localStorage.setItem("token",response?.data["access-token"])
                if(purpose&&product){
                    dispatch(AddToWishlistAction(response?.data?.user_id,product.id,navigation))
                }else{
                    toast.success(response?.data?.message, {
                        position: "top-center",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                        });
                   navigation(process.env.PUBLIC_URL+"/")
                }
            }
            else{         
                dispatch(AuthActions.LoginAPIFailure())
                toast.error(response?.data?.message, {
                    position: "top-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    });
            }
        })
        .catch(err => dispatch(AuthActions.LoginAPIFailure()))
    }
}


export const AuthActions = AuthSlice.actions;

export default AuthSlice.reducer;