import axios from "axios";
import { createSlice } from "@reduxjs/toolkit";
import { API_BASE_URL } from "../../../utils/constant";
import {CATEGORY_PRODUCT, PRODUCT_DETAIL} from "../../../utils/apiConstants";

const initialAuthState  = {
    isLoading: false,
    isError: null,
    categoryProduct: null,
    isSuccess: false,
    productDetail:null,
    VariantsRes:null
};

const ProductSlice = createSlice({
    name: 'Product',
    initialState: initialAuthState,
    reducers: {
        CategoryProductAPIRequest(state) {            
            state.isLoading = true;
        },
        CategoryProductAPISuccess(state, action){
            state.categoryProduct = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        CategoryProductAPIFailure(state, action){    
            state.isLoading = false;
        },


        ProductDetailAPIRequest(state) {            
            state.isLoading = true;
        },
        ProductDetailAPISuccess(state, action){
            state.productDetail = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        ProductDetailAPIFailure(state, action){    
            state.isLoading = false;
        },

        
        GetVariantsAPIRequest(state) {            
            state.isLoading = true;
        },
        GetVariantsAPISuccess(state, action){
            state.VariantsRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        GetVariantsAPIFailure(state, action){    
            state.isLoading = false;
        },
    }
});


export const CategoryProducts = (id) => {
    return async (dispatch) => {
        dispatch(ProductActions.CategoryProductAPIRequest());
        let url= `${API_BASE_URL}${CATEGORY_PRODUCT}/${id}`;   
        axios({
            method: 'GET',
            url: url,
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(ProductActions.CategoryProductAPISuccess(response?.data?.data))
            }
            else{         
                dispatch(ProductActions.CategoryProductAPIFailure())
            }
        })
        .catch(err => dispatch(ProductActions.CategoryProductAPIFailure()))
    }
}

export const ProductDetailAction = (id) => {
    return async (dispatch) => {
        dispatch(ProductActions.ProductDetailAPIRequest());
        let url= `${API_BASE_URL}${PRODUCT_DETAIL}/${id}`;   
        axios({
            method: 'GET',
            url: url,
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(ProductActions.ProductDetailAPISuccess(response?.data?.data?.product_detail))
            }
            else{         
                dispatch(ProductActions.ProductDetailAPIFailure())
            }
        })
        .catch(err => dispatch(ProductActions.ProductDetailAPIFailure()))
    }
}

export const GetVariantsAction = () => {
    return async (dispatch) => {
        dispatch(ProductActions.GetVariantsAPIRequest());
        let url= `${API_BASE_URL}productVariants`;   
        axios({
            method: 'GET',
            url: url,
        })
        .then(response => { 
            if(response.data.status == 1){
              dispatch(ProductActions.GetVariantsAPISuccess(response?.data?.data))
            }
            else{         
                dispatch(ProductActions.GetVariantsAPIFailure())
            }
        })
        .catch(err => dispatch(ProductActions.GetVariantsAPIFailure()))
    }
}



export const ProductActions = ProductSlice.actions;

export default ProductSlice.reducer;