import axios from "axios";
import { createSlice } from "@reduxjs/toolkit";
import { API_BASE_URL } from "../../../utils/constant";
import { CATEGORIES_LIST, COMPANY_DETAIL, HOME_MAIN, HOME_PRODUCTS } from "../../../utils/apiConstants";

const initialAuthState  = {
    isLoading: false,
    isError: null,
    categoriesListRes: null,
    isSuccess: false,
    HomeProductsRes:[] ,
    HomeMainRes:[],
    CompanyDetailRes:{}
};

const CategoriesListSlice = createSlice({
    name: 'Home_Page',
    initialState: initialAuthState,
    reducers: {
        CategoriesListAPIRequest(state) {            
            state.isLoading = true;
        },
        CategoriesListAPISuccess(state, action){
            state.categoriesListRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        CategoriesListAPIFailure(state, action){    
            state.isLoading = false;
        },

        HomeMainAPIRequest(state) {            
            state.isLoading = true;
        },
        HomeMainAPISuccess(state, action){
            state.HomeMainRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        HomeMainAPIFailure(state, action){    
            state.isLoading = false;
        },

        HomePageProductAPIRequest(state) {            
            state.isLoading = true;
        },
        HomePageProductAPISuccess(state, action){
            state.HomeProductsRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        HomePageProductAPIFailure(state, action){    
            state.isLoading = false;
        },

        CompanyDetailAPIRequest(state) {            
            state.isLoading = true;
        },
        CompanyDetailAPISuccess(state, action){
            state.CompanyDetailRes = action.payload;
            state.isSuccess = true;
            state.isLoading = false;    
        },
        CompanyDetailAPIFailure(state, action){    
            state.isLoading = false;
        },
    }
});


export const HomeMainAction = () => {
    return async (dispatch) => {
        dispatch(CategoriesListActions.HomeMainAPIRequest());
        let url= `${API_BASE_URL}${HOME_MAIN}`;   
        axios({
            method: 'GET',
            url: url,
        })
        .then(response => { 
            // console.log("HomeMain",response?.data?.data?.sliders)
            if(response.data.status == 1){
              dispatch(CategoriesListActions.HomeMainAPISuccess(response?.data?.data))
            }
            else{         
                dispatch(CategoriesListActions.HomeMainAPIFailure())
            }
        })
        .catch(err => dispatch(CategoriesListActions.HomeMainAPIFailure()))
    }
}

